export const getStudentTimetable = ({ studentId, fromDate, toDate } = {}) =>
  request({
    prefix,
    url: `/Portal/GetStudentTimetable`,
    method: 'GET',
    params: { studentId, fromDate, toDate },
    data: {},
  });

export const exportStudentsForAttendance = ({ classCreditScheduleId } = {}) =>
  requestDownload({
    prefix,
    url: `/Portal/ExportStudentsForAttendance`,
    method: 'POST',
    params: {},
    data: { classCreditScheduleId },
  });

export const exportStudentGrade = ({ classCreditScheduleId } = {}) =>
  requestDownload({
    prefix,
    url: `/GradePartGrading/ExportStudentGrade`,
    method: 'POST',
    params: {},
    data: { id: classCreditScheduleId },
  });



