import React, { useEffect, useState } from 'react';
import { Avatar, Row, Col } from 'antd';
import useCurrentUserId from '../../../hook/useCurrentUserId';
import { getStaffById } from '../../../services/staff';
import { getAvatarUrl } from '../../../services/filestorage';

export default () => {
  const currentUserId = useCurrentUserId();
  const [teacher, setTeacher] = useState({});

  const callGetStaffById = async () => {
    const [res] = await getStaffById({ id: currentUserId });
    setTeacher(res.data);
  };

  useEffect(() => {
    if (!currentUserId) {
      return;
    }
    callGetStaffById();
  }, [currentUserId]);

  return (
    <div className="profile" style={{ padding: 10 }}>
      <Row>
        <Col span={4}>
          <Avatar size={64} src={getAvatarUrl(teacher.avatarFileId)} />
        </Col>
        <Col span={20}>
          <p className="profile-name">{teacher.fullname}</p>
          <p>Mã: {teacher.staffId}</p>
          <p>Giới tính: {teacher.genderText}</p>
          <p>Ngày sinh: {teacher.birthdate}</p>
        </Col>
      </Row>
    </div>
  );
};



