import React from 'react';
import { Link } from 'react-router-dom';
import Btn from '../../../components/common/Btn/Btn';
import BtnGroup from '../../../components/common/Btn/BtnGroup';
import useCurrentUserId from '../../../hook/useCurrentUserId';
import TableData from '../../../react-uikit/component/table/TableData';
import { getTeacherClasses } from '../../../services/educationPortal';

export default () => {
  const currentUserId = useCurrentUserId();
  const columns = [
    {
      title: 'Mã',
      dataIndex: 'syllable',
      key: 'syllable',
    },
    {
      title: 'Tên',
      dataIndex: 'subject',
      key: 'subject',
    },
    {
      title: 'Số SV',
      dataIndex: 'noOfStudent',
      key: 'noOfStudent',
    },
    {
      title: '',
      dataIndex: 'action',
      key: 'action',
      render: (text, record) => {
        return (
          <BtnGroup>
            <Link
              to={{
                pathname: '/giao-vien/diem-danh',
                search: `?classId=${record.classCreditScheduleId}`,
              }}
            >
              <Btn primary>Điểm danh</Btn>
            </Link>
            <Link
              to={{
                pathname: '/giao-vien/cham-diem',
                search: `?classId=${record.classCreditScheduleId}`,
              }}
            >
              <Btn primary>Chấm điểm</Btn>
            </Link>
          </BtnGroup>
        );
      },
    },
  ];
  return (
    <div>
      <TableData
        pagination={false}
        columns={columns}
        paramSearch={{ teacherId: currentUserId }}
        service={getTeacherClasses}
      />
    </div>
  );
};



